rr.);\n                        item. = parseFloat(item.) + parseFloat(curr.);\n                    } else {\n                        curr. = parseFloat(curr.);\n                        curr. = parseFloat(curr.);\n                        acc[curr.] = curr;\n                    } // if else\n\n                    return acc;\n                }, {}));\n\n                all_data_sorted.buylist[i] = sum_result;\n            } // for\n\n            // sort the yearly inbound\n            for (let i = 0; i < 12; i++) {\n                let newArray = allRowsObj.inbound.filter(function (el) {\n                    let sql_Date_To_JS_Date = new Date(el..replace(' ', 'T'))\n                    return sql_Date_To_JS_Date.getMonth() == i;\n                });\n\n                // sum the entries with the same \n                let sum_result = Object.values(newArray.reduce((acc, curr) => {\n                    let item = acc[curr.];\n\n                    if (item) {\n                        item. = parseInt(item.) + parseInt(curr.);\n                    } else {\n                        curr. = parseInt(curr.);\n                        acc[curr.] = curr;\n                    } // if else\n\n                    return acc;\n                }, {}));\n\n                all_data_sorted.inbound[i] = sum_result;\n            } // for\n\n            console.log(all_data_sorted); // test\n            await SortCurrentMonthTable(monthTag.value, data);\n            await CalChartDatasets();\n\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n            table.isLoading = false;\n        }); // watch for data change\n\n        const updateCheckedRows = (rowsKey) => {\n            checkedRows.length = 0;\n            checkedRows.push(...rowsKey);\n            // console.log(checkedRows); // test\n        };\n\n        return {\n            table,\n            searchTerm,\n            updateCheckedRows,\n            OutputExcelClick,\n            yearTag,\n            monthStr\n        };\n    }, // setup\n});\n</script>\n<style scoped>\n::v-deep(.vtl-table .vtl-thead .vtl-thead-th) {\n    color: #fff;\n    background-color: #196241;\n    border-color: #196241;\n}\n</style>"],"sourceRoot":""}]);
