           '\" name=\"number' +\n                                i +\n                                '\" value=\"' +\n                                row. +\n                                '\">' +\n                                '<div class=\"text-nowrap CustomScrollbar\"' +\n                                ' style=\"overflow-x: auto; width: 100%;\">' +\n                                row. +\n                                \"</div>\"\n                            );\n                        } // if else\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"inboundpageLang.inboundnum\"\n                    ),\n                    field: \"\",\n                    width: \"14ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        if (row. === null) {\n                            return (\n                                '<input type=\"hidden\" id=\"inboundnum' +\n                                i +\n                                '\" name=\"inboundnum' +\n                                i +\n                                '\" value=\"' +\n                                row. +\n                                '\">' +\n                                '<div class=\"CustomScrollbar text-nowrap\"' +\n                                ' style=\"overflow-x: auto; width: 100%;\">' +\n                                row. + ' <small class=\"text-danger\">' +\n                                app.appContext.config.globalProperties.$t(\"inboundpageLang.no_basic\") + \"</small>\" +\n                                \"</div>\"\n                            );\n                        } else {\n                            return (\n                                '<input type=\"hidden\" id=\"inboundnum' +\n                                i +\n                                '\" name=\"inboundnum' +\n                                i +\n                                '\" value=\"' +\n                                row. +\n                                '\">' +\n                                '<div class=\"CustomScrollbar text-nowrap\"' +\n                                ' style=\"overflow-x: auto; width: 100%;\">' +\n                                row. + \" <small>\" + row. + \"</small>\" +\n                                \"</div>\"\n                            );\n                        } // if else\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"inboundpageLang.loc\"\n                    ),\n                    field: \"\",\n                    width: \"10ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"position' +\n                            i +\n                            '\" name=\"position' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"inboundpageLang.inpeople\"\n                    ),\n                    field: \"\",\n                    width: \"14ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"inboundpeople' +\n                            i +\n                            '\" name=\"inboundpeople' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"inboundpageLang.inreason\"\n                    ),\n                    field: \"\",\n                    width: \"15ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"inboundreason' +\n                            i +\n                            '\" name=\"inboundreason' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"inboundpageLang.inboundtime\"\n                    ),\n                    field: \"\",\n                    width: \"12ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"inboundtime' +\n                            row.id +\n                            '\" name=\"inboundtime' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"inboundpageLang.mark\"\n                    ),\n                    field: \"\",\n                    width: \"10ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        if (row. === null) row. = \"\";\n                        return (\n                            '<input type=\"hidden\" id=\"remark' +\n                            i +\n                            '\" name=\"remark' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n            ],\n            rows: computed(() => {\n                return data.filter((x) =>\n                    x.\n                        .toLowerCase()\n                        .includes(searchTerm.value.toLowerCase())\n                );\n            }),\n            totalRecordCount: computed(() => {\n                return table.rows.length;\n            }),\n            sortable: {\n                order: \"\",\n                sort: \"desc\",\n            },\n            messages: {\n                pagingInfo:\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.now_showing\"\n                    ) +\n                    \" {0} ~ {1} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ) +\n                    \", \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.total\"\n                    ) +\n                    \" {2} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ),\n                pageSizeChangeLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.records_per_page\"\n                ),\n                gotoPageLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.go_to_page\"\n                ),\n                noDataAvailable: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.search_with_no_data_returned\"\n                ),\n            },\n            pageOptions: [\n                {\n                    value: 10,\n                    text: 10,\n                },\n                {\n                    value: 20,\n                    text: 20,\n                },\n                {\n                    value: 40,\n                    text: 40,\n                },\n                {\n                    value: 100,\n                    text: 100,\n                },\n            ],\n        });\n\n        const updateCheckedRows = (rowsKey) => {\n            console.log(rowsKey); // test\n            checkedRows = rowsKey;\n        };\n\n        return {\n            searchTerm,\n            table,\n            updateCheckedRows,\n            DeleteRowsClick,\n            OutputExcelClick\n        };\n    }, // setup\n});\n</script>\n<style scoped>\n::v-deep(.vtl-table .vtl-thead .vtl-thead-th) {\n    color: #fff;\n    background-color: #196241;\n    border-color: #196241;\n}\n</style>"],"sourceRoot":""}]);
