                           <div class=\"col col-auto\">\n                                <label for=\"pnInput\" class=\"col-form-label\">{{ $t(\"basicInfoLang.quicksearch\") }}\n                                    :</label>\n                            </div>\n                            <div class=\"col col-auto p-0 m-0\">\n                                <input id=\"pnInput\" class=\"text-center form-control form-control-lg\"\n                                    v-bind:placeholder=\"$t('monthlyPRpageLang.enterisn_or_descr')\"\n                                    v-model=\"searchTerm2\" />\n                            </div>\n                        </div>\n                        <div class=\"col col-auto\">\n                            <button id=\"download\" name=\"download\" class=\"col col-auto btn btn-lg btn-success\"\n                                :value=\"$t('monthlyPRpageLang.download')\" @click=\"OutputExcelClick(modalTitle)\">\n                                <i class=\"bi bi-file-earmark-arrow-down-fill fs-4\"></i>\n                            </button>\n                        </div>\n                    </div>\n                    <div class=\"w-100\" style=\"height: 1ch\"></div>\n                    <!-- </div>breaks cols to a new line-->\n                    <table-lite :is-static-mode=\"true\" :isSlotMode=\"true\" :hasCheckbox=\"false\"\n                        :messages=\"table2.messages\" :columns=\"table2.columns\" :rows=\"table2.rows\"\n                        :total=\"table2.totalRecordCount\" :page-options=\"table2.pageOptions\" :sortable=\"table2.sortable\"\n                        :is-fixed-first-column=\"false\" @row-clicked=\"rowClicked\">\n                    </table-lite>\n                </div>\n                <div v-if=\"showFooter\" class=\"modal-footer justify-content-between\">\n                    <button @click=\"sxb_reject\" type=\"button\" class=\"btn btn-lg btn-danger\" style=\"border-radius: 5px;\">\n                        {{ $t('monthlyPRpageLang.review_cancel') }}\n                    </button>\n                    <button @click=\"sxb_approve\" type=\"button\" class=\"btn btn-lg btn-success\"\n                        style=\"border-radius: 5px;\">\n                        {{ $t('monthlyPRpageLang.review_complete') }}\n                    </button>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { defineComponent, reactive, ref, computed } from \"vue\";\nimport {\n    getCurrentInstance,\n    onBeforeMount,\n    onMounted,\n    watch,\n} from \"@vue/runtime-core\";\nimport * as XLSX from 'xlsx';\nimport TableLite from \"./TableLite.vue\";\nimport useSxbSearch from \"../../composables/SxbSearch.ts\";\nexport default defineComponent({\n    name: \"App\",\n    components: { TableLite },\n    setup() {\n        const { mats_SXB, inTransit, getMats, SXB_Reject, SXB_Approve, getTransit } = useSxbSearch(); // axios get the mats_SXB data\n\n        onBeforeMount(getMats);\n\n        const searchTerm = ref(\"\"); // Search text\n        const searchTerm2 = ref(\"\"); // Search text for modal table\n        const modalTitle = ref(\"\");\n        let showFooter = ref(false);\n        let AllRecords = [];\n\n        const app = getCurrentInstance(); // get the current instance\n        let thisHtmlLang = document\n            .getElementsByTagName(\"HTML\")[0]\n            .getAttribute(\"lang\");\n        // get the current locale from html tag\n        app.appContext.config.globalProperties.$lang.setLocale(thisHtmlLang); // set the current locale to vue package\n\n        // pour the data in\n        const data = reactive([]);\n        const data2 = reactive([]);\n\n        const triggerModal = async () => {\n            $(\"body\").loadingModal({\n                text: \"Loading...\",\n                animation: \"circle\",\n            });\n\n            return new Promise((resolve, reject) => {\n                resolve();\n            });\n        } // triggerModal\n\n        const OutputExcelClick = async (output_range) => {\n            await triggerModal();\n\n            // get today's date for filename\n            let today = new Date();\n            let dd = String(today.getDate()).padStart(2, '0');\n            let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!\n            let yyyy = today.getFullYear();\n            today = yyyy + \"_\" + mm + '_' + dd;\n\n            if (output_range === 'All') {\n                let rows = Array();\n                for (let i = 0; i < AllRecords.length; i++) {\n                    let tempObj = new Object;\n                    tempObj. = AllRecords[i].SXB;\n                    tempObj. = AllRecords[i].;\n                    tempObj. = AllRecords[i].;\n                    tempObj.MOQ = AllRecords[i].MOQ;\n                    tempObj. = AllRecords[i].;\n                    tempObj. = parseFloat(AllRecords[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\") + \" \" + AllRecords[i].;\n                    tempObj. = AllRecords[i].;\n\n                    rows.push(tempObj);\n                } // for\n\n                const worksheet = XLSX.utils.json_to_sheet(rows);\n                const workbook = XLSX.utils.book_new();\n                XLSX.utils.book_append_sheet(workbook, worksheet, 'ALL');\n                XLSX.writeFile(workbook,\n                    app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.SXB_search\"\n                    ) + \"(ALL)_\" + today + \".xlsx\", { compression: true });\n            } else {\n                let rows = Array();\n                for (let i = 0; i < data2.length; i++) {\n                    let tempObj = new Object;\n                    tempObj. = data2[i].SXB;\n                    tempObj. = data2[i].;\n                    tempObj. = data2[i].;\n                    tempObj.MOQ = data2[i].MOQ;\n                    tempObj. = data2[i].;\n                    tempObj. = parseFloat(data2[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\") + \" \" + data2[i].;\n                    tempObj. = data2[i].;\n\n                    rows.push(tempObj);\n                } // for\n\n                const worksheet = XLSX.utils.json_to_sheet(rows);\n                const workbook = XLSX.utils.book_new();\n                XLSX.utils.book_append_sheet(workbook, worksheet, data2[0].SXB);\n                XLSX.writeFile(workbook,\n                    app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.SXB_search\"\n                    ) + \"(\" + data2[0].SXB + \")_\" + today + \".xlsx\", { compression: true });\n            } // if else\n\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n        } // OutputExcelClick\n\n        const openSXBDetails = (SXB) => {\n            // console.log(\"clicked!\"); // test\n            modalTitle.value = SXB;\n            data2.splice(0);\n            for (let i = 0; i < AllRecords.length; i++) {\n                if (AllRecords[i].SXB === SXB) {\n                    data2.push(AllRecords[i]);\n                } // if\n            } // for\n\n            if (data2[0]. === '') {\n                showFooter.value = true;\n            } // if\n            else {\n                showFooter.value = false;\n            } // else\n        } // openSXBDetails\n\n        const sxb_reject = async () => {\n            await triggerModal();\n            let result = await SXB_Reject(data2[0].SXB);\n            if (result === \"success\") {\n                showFooter.value = false;\n                notyf.open({\n                    type: \"success\",\n                    message: app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.change\") + \" \" + app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.success\"),\n                    duration: 3000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n\n                await getMats();\n            } // if\n            else {\n                notyf.open({\n                    type: \"error\",\n                    message: app.appContext.config.globalProperties.$t(\"checkInvLang.update_failed\"),\n                    duration: 3000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n            } // else\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n        } // sxb_reject\n\n        const sxb_approve = async () => {\n            await triggerModal();\n            let temp_isn = [];\n            for (let i = 0; i < data2.length; i++) {\n                temp_isn.push(data2[i].);\n            } // for\n\n            await getTransit(temp_isn); // get existing in-transit data\n            let existing = JSON.parse(inTransit.value).data;\n            // console.log(existing); // test\n            // return ; // test\n            let isn = [];\n            let amount = [];\n            for (let i = 0; i < data2.length; i++) {\n                isn.push(data2[i].);\n\n                let indexOfObject = existing.findIndex(object => {\n                    return (object. === data2[i].);\n                });\n\n                if (indexOfObject === -1) {\n                    amount.push(parseInt(data2[i].));\n                } // if\n                else {\n                    amount.push(parseInt(data2[i].) + parseInt(existing[indexOfObject].));\n                } // else\n            } // for\n\n            // console.log(amount, isn); // test\n\n            let result = await SXB_Approve(data2[0].SXB, isn, amount);\n            if (result === \"success\") {\n                showFooter.value = false;\n                notyf.open({\n                    type: \"success\",\n                    message: app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.change\") + \" \" + app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.success\"),\n                    duration: 3000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n\n                await getMats();\n            } // if\n            else {\n                notyf.open({\n                    type: \"error\",\n                    message: app.appContext.config.globalProperties.$t(\"checkInvLang.update_failed\"),\n                    duration: 3000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n            } // else\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n        } // sxb_approve\n\n        watch(mats_SXB, async () => {\n            await triggerModal();\n            // console.log(JSON.parse(mats_SXB.value)); // test\n            let allRowsObj = JSON.parse(mats_SXB.value);\n            data.splice(0);\n            AllRecords = [];\n            for (let i = 0; i < allRowsObj.datas.length; i++) {\n                allRowsObj.datas[i]. = parseInt(\n                    allRowsObj.datas[i].\n                );\n\n                allRowsObj.datas[i]. = allRowsObj.datas[i].SRM;\n                allRowsObj.datas[i].id = i + 1;\n                if (allRowsObj.datas[i]. === null || allRowsObj.datas[i]. === undefined) allRowsObj.datas[i]. = \"N/A\";\n                AllRecords.push(allRowsObj.datas[i]);\n                let indexOfObject = data.findIndex(object => {\n                    return (object.SXB === allRowsObj.datas[i].SXB);\n                });\n\n                if (indexOfObject === -1) {\n                    data.push(allRowsObj.datas[i]);\n                } // if\n            } // for\n\n            // console.log(allRowsObj.datas); // test\n            // console.log(AllRecords); // test\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n            table.isLoading = false;\n        }); // watch for data change\n\n        // Table config\n        const table = reactive({\n            isLoading: true,\n            columns: [\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.buytime\"\n                    ),\n                    field: \"\",\n                    width: \"15ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        let returnStr = \"\";\n                        // console.log(row); // test\n                        returnStr =\n                            '<input type=\"hidden\" id=\"srm' +\n                            i +\n                            '\" name=\"srm' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\";\n\n                        return returnStr;\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.status\"\n                    ),\n                    field: \"\",\n                    width: \"6ch\",\n                    sortable: true,\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.info\"\n                    ),\n                    field: \"SXB\",\n                    width: \"4ch\",\n                    sortable: false,\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.pr_sender\"\n                    ),\n                    field: \"\",\n                    width: \"8ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        if (row. === null || row. === undefined) row. = \"N/A\";\n                        return (\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n            ],\n            rows: computed(() => {\n                return data.filter((x) =>\n                \n                    x.\n                        .toLowerCase()\n                        .includes(searchTerm.value.toLowerCase())\n                );\n            }),\n            totalRecordCount: computed(() => {\n                return table.rows.length;\n            }),\n            sortable: {\n                order: \"\",\n                sort: \"desc\",\n            },\n            messages: {\n                pagingInfo:\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.now_showing\"\n                    ) +\n                    \" {0} ~ {1} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ) +\n                    \", \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.total\"\n                    ) +\n                    \" {2} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ),\n                pageSizeChangeLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.records_per_page\"\n                ),\n                gotoPageLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.go_to_page\"\n                ),\n                noDataAvailable: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.search_with_no_data_returned\"\n                ),\n            },\n            pageOptions: [\n                {\n                    value: 10,\n                    text: 10,\n                },\n                {\n                    value: 20,\n                    text: 20,\n                },\n                {\n                    value: 40,\n                    text: 40,\n                },\n                {\n                    value: 100,\n                    text: 100,\n                },\n            ],\n        });\n\n        const table2 = reactive({\n            isLoading: true,\n            columns: [\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.isn\"\n                    ),\n                    field: \"\",\n                    width: \"14ch\",\n                    sortable: true,\n                    isKey: true,\n                    display: function (row, i) {\n                        // console.log(row);\n                        return (\n                            '<input type=\"hidden\" id=\"number' +\n                            row.id +\n                            '\" name=\"number' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.pName\"\n                    ),\n                    field: \"\",\n                    width: \"13ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"name' +\n                            i +\n                            '\" name=\"name' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.moq\"\n                    ),\n                    field: \"MOQ\",\n                    width: \"8ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"moq' +\n                            row.id +\n                            '\" name=\"moq' +\n                            i +\n                            '\" value=\"' +\n                            row.MOQ +\n                            '\">' +\n                            '<div class=\"CustomScrollbar text-nowrap\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row.MOQ + \" <small>\" + row. + \"</small>\" +\n                            \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.buyamount\"\n                    ),\n                    field: \"\",\n                    width: \"12ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"buyamount' +\n                            row.id +\n                            '\" name=\"buyamount' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            parseInt(row.).toLocaleString(\"en-US\") +\n                            \" <small>\" + row. + \"</small>\" + \"</div>\"\n                        );\n                    },\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.buyprice\"\n                    ),\n                    field: \"\",\n                    width: \"11ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        return (\n                            '<input type=\"hidden\" id=\"buyprice' +\n                            row.id +\n                            '\" name=\"buyprice' +\n                            i +\n                            '\" value=\"' +\n                            row. +\n                            '\">' +\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            parseFloat(row.).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\") +\n                            \" <small>\" + row. + \"</small>\" +\n                            \"</div>\"\n                        );\n                    },\n                },\n            ],\n            rows: computed(() => {\n                return data2.filter((x) =>\n                    x.\n                        .toLowerCase()\n                        .includes(searchTerm2.value.toLowerCase()) ||\n                    x.\n                        .includes(searchTerm2.value)\n                );\n            }),\n            totalRecordCount: computed(() => {\n                return table2.rows.length;\n            }),\n            sortable: {\n                order: \"\",\n                sort: \"desc\",\n            },\n            messages: {\n                pagingInfo:\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.now_showing\"\n                    ) +\n                    \" {0} ~ {1} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ) +\n                    \", \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.total\"\n                    ) +\n                    \" {2} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ),\n                pageSizeChangeLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.records_per_page\"\n                ),\n                gotoPageLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.go_to_page\"\n                ),\n                noDataAvailable: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.search_with_no_data_returned\"\n                ),\n            },\n            pageOptions: [\n                {\n                    value: 10,\n                    text: 10,\n                },\n                {\n                    value: 20,\n                    text: 20,\n                },\n                {\n                    value: 40,\n                    text: 40,\n                },\n                {\n                    value: 100,\n                    text: 100,\n                },\n            ],\n        });\n\n        /**\n        * Row clicked event\n        */\n        const rowClicked = (row) => {\n            // console.log(\"Row clicked!\", row);\n        };\n\n        return {\n            searchTerm,\n            searchTerm2,\n            table,\n            table2,\n            modalTitle,\n            showFooter,\n            rowClicked,\n            openSXBDetails,\n            sxb_approve,\n            sxb_reject,\n            OutputExcelClick,\n        };\n    }, // setup\n});\n</script>\n<style scoped>\n::v-deep(.vtl-table .vtl-thead .vtl-thead-th) {\n    color: #fff;\n    background-color: #196241;\n    border-color: #196241;\n}\n</style>"],"sourceRoot":""}]);
