___CSS_LOADER_EXPORT___.push([module.id, ".flip-card[data-v-57b1c3ff]{background-color:transparent;height:75vh;overflow-y:auto;perspective:1000px}.flip-card[data-v-57b1c3ff]::-webkit-scrollbar-track{background-color:#f5f5f5;border-radius:4px;-webkit-box-shadow:0 0 1px hsla(0,0%,100%,.5)}.flip-card[data-v-57b1c3ff]::-webkit-scrollbar{-webkit-appearance:none;width:4px}.flip-card[data-v-57b1c3ff]::-webkit-scrollbar-thumb{background-color:rgba(0,0,0,.3);border-radius:4px;-webkit-box-shadow:0 0 1px hsla(0,0%,100%,.5)}.flip-card-inner[data-v-57b1c3ff]{position:relative;transform-style:preserve-3d;transition:transform .5s}.flip-card.transition .flip-card-inner[data-v-57b1c3ff]{transform:rotateY(180deg)}.flip-card-back[data-v-57b1c3ff],.flip-card-front[data-v-57b1c3ff]{backface-visibility:hidden;position:absolute}.flip-card-back[data-v-57b1c3ff]{transform:rotateY(180deg)}", "",{"version":3,"sources":["webpack://./resources/js/components/reusableUtilities/NonMonthlyPRTable.vue"],"names":[],"mappings":"AAmGA,4BACI,4BAA6B,CAG7B,WAAY,CACZ,eAAgB,CAHhB,kBAIJ,CAEA,qDAGI,wBAAyB,CADzB,iBAAkB,CADlB,6CAGJ,CAEA,+CAEI,uBAAwB,CADxB,SAEJ,CAEA,qDAGI,+BAAoC,CAFpC,iBAAkB,CAClB,6CAEJ,CAGA,kCACI,iBAAkB,CAElB,2BAA4B,CAD5B,wBAEJ,CAGA,wDACI,yBACJ,CAGA,mEAII,0BAA2B,CAF3B,iBAGJ,CAEA,iCACI,yBACJ","sourcesContent":["<template>\n    <div class=\"flip-card\" ref=\"detectclasschange\">\n        <div class=\"flip-card-inner row w-100\">\n            <div class=\"flip-card-front col col-12\" id=\"cardFront\" style=\"height: auto !important;\">\n                <NonMonthlyPRImportTable ref=\"myChild\"></NonMonthlyPRImportTable>\n            </div>\n            <div class=\"flip-card-back col col-12\" id=\"cardBack\">\n                <NonMonthlyPRSearchTable ref=\"myChild2\"></NonMonthlyPRSearchTable>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { defineComponent, nextTick, reactive, ref, computed } from \"vue\";\nimport {\n    getCurrentInstance,\n    onBeforeMount,\n    onBeforeUnmount,\n    onMounted,\n    watch,\n} from \"@vue/runtime-core\";\n// import TableLite from \"./TableLite.vue\";\nimport NonMonthlyPRImportTable from \"./NonMonthlyPRImportTable.vue\";\nimport NonMonthlyPRSearchTable from \"./NonMonthlyPRSearchTable.vue\";\nexport default defineComponent({\n    name: \"App\",\n    components: { NonMonthlyPRImportTable, NonMonthlyPRSearchTable },\n    setup() {\n        const myChild = ref(null);\n        const myChild2 = ref(null);\n        const detectclasschange = ref(null);\n\n        const observer = new MutationObserver(mutations => {\n            // Whenever the search page is shown,\n            // update the table using \"onclasschange\"\n            // and trigger child component's function \"triggerSearchUpdate\"\n            for (const m of mutations) {\n                const newValue = m.target.getAttribute(m.attributeName);\n                nextTick(() => {\n                    onclasschange(newValue, m.oldValue);\n                });\n            } // for\n        });\n\n        onBeforeUnmount(observer.disconnect());\n\n        onMounted(() => {\n            observer.observe(detectclasschange.value, {\n                attributes: true,\n                attributeOldValue: true,\n                attributeFilter: ['class'],\n            });\n\n            watch(\n                () => myChild.value.flip,\n                (newVal) => {\n                    $('.flip-card').toggleClass('transition');\n                }\n            );\n\n            watch(\n                () => myChild2.value.flip,\n                (newVal) => {\n                    $('.flip-card').toggleClass('transition');\n                }\n            );\n        });\n\n        const triggerModal = async () => {\n            $(\"body\").loadingModal({\n                text: \"Loading...\",\n                animation: \"circle\",\n            });\n\n            return new Promise((resolve, reject) => {\n                resolve();\n            });\n        } // triggerModal\n\n        const onclasschange = async (classattrvalue) => {\n            await triggerModal();\n            const classlist = classattrvalue.split(' ');\n            if (classlist.includes('transition')) {\n                await myChild2.value.triggerSearchUpdate();\n            } // if\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n        } // onclasschange\n\n        return {\n            myChild,\n            myChild2,\n            detectclasschange,\n        };\n    }, // setup\n});\n</script>\n<style scoped>\n.flip-card {\n    background-color: transparent;\n    perspective: 1000px;\n    /* Remove this if you don't want the 3D effect */\n    height: 75vh;\n    overflow-y: auto;\n}\n\n.flip-card::-webkit-scrollbar-track {\n    -webkit-box-shadow: 0 0 1px hsla(0, 0%, 100%, .5);\n    border-radius: 4px;\n    background-color: #F5F5F5;\n}\n\n.flip-card::-webkit-scrollbar {\n    width: 4px;\n    -webkit-appearance: none;\n}\n\n.flip-card::-webkit-scrollbar-thumb {\n    border-radius: 4px;\n    -webkit-box-shadow: 0 0 1px hsla(0, 0%, 100%, .5);\n    background-color: rgba(0, 0, 0, 0.3);\n}\n\n/* This container is needed to position the front and back side */\n.flip-card-inner {\n    position: relative;\n    transition: transform 0.5s;\n    transform-style: preserve-3d;\n}\n\n/* Do an horizontal flip when you move the mouse over the flip box container */\n.flip-card.transition .flip-card-inner {\n    transform: rotateY(180deg);\n}\n\n/* Position the front and back side */\n.flip-card-front,\n.flip-card-back {\n    position: absolute;\n    -webkit-backface-visibility: hidden;\n    backface-visibility: hidden;\n}\n\n.flip-card-back {\n    transform: rotateY(180deg);\n}\n</style>"],"sourceRoot":""}]);
