            checkedRows.length = 0;
            checkedRows.push(...rowsKey);
            // console.log(checkedRows); // test
        };

        return {
            table,
            searchTerm,
            updateCheckedRows,
            OutputExcelClick,
            yearTag,
            monthStr
        };
    }, // setup
});
</script>
