,iBAAkB,CAGlB,KAAM,CACN,UAEF,CAEA,4EAGE,8BAA+B,CAF/B,UAAW,CAMX,WAAY,CAHZ,MAAO,CAFP,iBAAkB,CAGlB,KAAM,CACN,UAEF,CAEA,8HAGE,SAAU,CADV,eAEF,CAEA,+DACE,SACF,CAEA,sHAEE,qBACF,CAEA,uBACE,YACF,CAEA,4BACE,sBAAuB,CACvB,wBACF,CAEA,iCACE,cACF,CAEA,6BACE,wBACF,CAEA,yBACE,YACF,CAEA,uBACE,iBACF,CAEA,oBACE,iBACF","sourcesContent":["<template>\n  <div class=\"vtl vtl-card p-2\">\n    <div class=\"vtl-card-title\" v-if=\"title\">{{ title }}</div>\n    <div class=\"vtl-card-body\">\n      <div class=\"vtl-row\">\n        <div class=\"col-sm-12 dataTables_wrapper CustomScrollbar\" :class=\"{\n          'fixed-first-column': isFixedFirstColumn,\n          'fixed-first-second-column': isFixedFirstColumn && hasCheckbox,\n        }\">\n          <div v-if=\"isLoading\" class=\"vtl-loading-mask\">\n            <div class=\"vtl-loading-content\">\n              <span style=\"color: white\">Loading...</span>\n            </div>\n          </div>\n          <table\n            class=\"vtl-table vtl-table-bordered table table-hover align-items-center align-middle text-center justify-content-center\"\n            ref=\"localTable\" :style=\"'max-height: ' + maxHeight + 'px;'\">\n            <thead class=\"vtl-thead\">\n              <tr class=\"vtl-thead-tr m-0 p-0\">\n                <th v-if=\"hasCheckbox\" class=\"vtl-thead-th vtl-checkbox-th m-0 p-0 align-items-center align-middle\"\n                  @click=\"clickChild($event)\">\n                  <input type=\"checkbox\" class=\"vtl-thead-checkbox\" v-model=\"setting.isCheckAll\" />\n                </th>\n                <th v-for=\"(col, index) in columns\" class=\"vtl-thead-th m-0 p-0\" :class=\"col.headerClasses\" :key=\"index\"\n                  :style=\"Object.assign(\n                    {\n                      width: col.width ? col.width : 'auto',\n                    },\n                    col.headerStyles\n                  )\n                    \" @click=\"clickChild($event)\">\n                  <div class=\"vtl-thead-column\" :class=\"{\n                    'vtl-sortable': col.sortable,\n                    'vtl-both': col.sortable,\n                    'vtl-asc':\n                      setting.order === col.field && setting.sort === 'asc',\n                    'vtl-desc':\n                      setting.order === col.field && setting.sort === 'desc',\n                  }\" @click.prevent=\"col.sortable ? doSort(col.field) : false\" v-html=\"col.label\">\n                  </div>\n                </th>\n              </tr>\n            </thead>\n            <template v-if=\"rows.length > 0\">\n              <tbody v-if=\"isStaticMode\" class=\"vtl-tbody\"\n                :set=\"(templateRows = groupingKey == '' ? [localRows] : localRows)\">\n                <template v-for=\"(rows, groupingIndex) in templateRows\">\n                  <tr :key=\"groupingIndex\" v-if=\"groupingKey != ''\" class=\"vtl-tbody-tr vtl-group-tr m-0 p-0\">\n                    <td :colspan=\"hasCheckbox ? 2 : 1\" class=\"vtl-tbody-td vtl-group-td m-0 p-0\">\n                      <div class=\"flex\">\n                        <div v-if=\"hasGroupToggle\" class=\"animation\">\n                          <a :ref=\"(el) => (toggleButtonRefs[groupingIndex] = el)\" class=\"cursor-pointer\"\n                            style=\"text-decoration: none; /* no underline */\"\n                            @click.prevent=\"toggleGroup(groupingIndex)\"></a>\n                        </div>\n                        <div class=\"ml-2\" v-html=\"groupingDisplay ? groupingDisplay(groupingIndex) : groupingIndex\"></div>\n                      </div>\n                    </td>\n                  </tr>\n                  <tr v-for=\"(row, i) in rows\" :key=\"row[setting.keyColumn] ? row[setting.keyColumn] : i\" :ref=\"(el) => {\n                    if (!groupingRowsRefs[groupingIndex]) {\n                      groupingRowsRefs[groupingIndex] = [];\n                    }\n                    groupingRowsRefs[groupingIndex][i] = el;\n                  }\" :name=\"'vtl-group-' + groupingIndex\" class=\"vtl-tbody-tr m-0 p-0\"\n                    :class=\"typeof rowClasses === 'function' ? rowClasses(row) : rowClasses\"\n                    @click=\"$emit('row-clicked', row)\">\n                    <td v-if=\"hasCheckbox\" class=\"vtl-tbody-td vtl-checkbox-td m-0 p-0\" @click=\"clickChild($event)\">\n                      <input type=\"checkbox\" class=\"vtl-tbody-checkbox\" :id=\"'checkbox' + i\" @click=\"checked(row, $event)\"\n                        :ref=\"(el) => { rowCheckbox.push(el); }\" :value=\"row[setting.keyColumn]\" />\n                    </td>\n                    <td v-for=\"(col, j) in columns\" :key=\"j\" class=\"vtl-tbody-td px-1 py-0\"\n                      :class=\"['vtl-tbody-td' + j].concat(col.columnClasses)\" :style=\"col.columnStyles\">\n                      <div v-if=\"col.display\" v-html=\"col.display(row, i)\"></div>\n                      <div v-else style=\"width: max-content; margin: 0 auto;\">\n                        <div v-if=\"setting.isSlotMode && slots[col.field]\" style=\"width: max-content; margin: 0 auto;\">\n                          <slot :name=\"col.field\" :row=\"row\" :key=\"i\"></slot>\n                        </div>\n                        <span v-else>{{ row[col.field] }}</span>\n                      </div>\n                    </td>\n                  </tr>\n                </template>\n              </tbody>\n              <tbody v-else :set=\"(templateRows = groupingKey == '' ? [rows] : groupingRows)\">\n                <template v-for=\"(rows, groupingIndex) in templateRows\">\n                  <tr v-if=\"groupingKey != ''\" class=\"vtl-tbody-tr vtl-group-tr m-0 p-0\" :key=\"groupingIndex\">\n                    <td :colspan=\"hasCheckbox ? columns.length + 1 : columns.length\"\n                      class=\"vtl-tbody-td vtl-group-td m-0 p-0\">\n                      <div class=\"flex\">\n                        <div v-if=\"hasGroupToggle\" class=\"animation\">\n                          <a :ref=\"(el) => (toggleButtonRefs[groupingIndex] = el)\" class=\"cursor-pointer\"\n                            style=\"text-decoration: none; /* no underline */\"\n                            @click.prevent=\"toggleGroup(groupingIndex)\"></a>\n                        </div>\n                        <div class=\"ml-2\" v-html=\"groupingDisplay\n                          ? groupingDisplay(groupingIndex)\n                          : groupingIndex\n                          \"></div>\n                      </div>\n                    </td>\n                  </tr>\n                  <tr v-for=\"(row, i) in rows\" :ref=\"(el) => {\n                    if (!groupingRowsRefs[groupingIndex]) {\n                      groupingRowsRefs[groupingIndex] = [];\n                    }\n                    groupingRowsRefs[groupingIndex][i] = el;\n                  }\n                    \" :name=\"'vtl-group-' + groupingIndex\" :key=\"row[setting.keyColumn] ? row[setting.keyColumn] : i\"\n                    class=\"vtl-tbody-tr m-0 p-0\" :class=\"typeof rowClasses === 'function' ? rowClasses(row) : rowClasses\"\n                    @click=\"$emit('row-clicked', row)\">\n                    <td v-if=\"hasCheckbox\" class=\"vtl-tbody-td vtl-checkbox-td m-0 p-0\" @click=\"clickChild($event)\">\n                      <input type=\"checkbox\" class=\"vtl-tbody-checkbox\" :id=\"'checkbox' + i\"\n                        :ref=\"(el) => { rowCheckbox.push(el); }\" :value=\"row[setting.keyColumn]\"\n                        @click=\"checked(row, $event)\" />\n                    </td>\n                    <td v-for=\"(col, j) in columns\" :key=\"j\" class=\"vtl-tbody-td px-1 py-0\"\n                      :class=\"['vtl-tbody-td' + j].concat(col.columnClasses)\" :style=\"col.columnStyles\">\n                      <div v-if=\"col.display\" v-html=\"col.display(row, i)\"></div>\n                      <div v-else style=\"width: max-content; margin: 0 auto;\">\n                        <div v-if=\"setting.isSlotMode && slots[col.field]\" style=\"width: max-content; margin: 0 auto;\">\n                          <slot :name=\"col.field\" :row=\"row\" :key=\"i\"></slot>\n                        </div>\n                        <span v-else>{{ row[col.field] }}</span>\n                      </div>\n                    </td>\n                  </tr>\n                </template>\n              </tbody>\n            </template>\n          </table>\n        </div>\n      </div>\n      <div class=\"vtl-paging vtl-row\" v-if=\"rows.length > 0\">\n        <template v-if=\"!setting.isHidePaging\">\n          <div class=\"vtl-paging-info col col-auto p-0 m-0\">\n            <div role=\"status\" aria-live=\"polite\">\n              {{\n                stringFormat(\n                  messages.pagingInfo,\n                  setting.offset,\n                  setting.limit,\n                  total\n                )\n              }}\n            </div>\n          </div>\n          <div class=\"w-100\" style=\"height: 1ch;\"></div>\n          <!-- </div>breaks cols to a new line-->\n          <div class=\"row w-100 justify-content-between align-items-center p-0 m-0\">\n            <div class=\"vtl-paging-change-div col col-auto p-0 m-0\">\n              <span class=\"vtl-paging-count-label col col-auto\">{{\n                messages.pageSizeChangeLabel\n              }}</span>\n              <select class=\"vtl-paging-count-dropdown\" v-model=\"setting.pageSize\">\n                <option v-for=\"pageOption in pageOptions\" :value=\"pageOption.value\" :key=\"pageOption.value\">\n                  {{ pageOption.text }}\n                </option>\n              </select>\n              &nbsp;&nbsp;\n              <span class=\"vtl-paging-page-label col col-auto\">{{\n                messages.gotoPageLabel\n              }}</span>\n              <select class=\"vtl-paging-page-dropdown col col-auto\" v-model=\"setting.page\">\n                <option v-for=\"n in setting.maxPage\" :key=\"n\" :value=\"parseInt(n)\">\n                  {{ n }}\n                </option>\n              </select>\n            </div>\n\n            <div class=\"\n                vtl-paging-pagination-div\n                align-items-center\n                col col-auto\n                p-0\n                m-0\n              \">\n              <div class=\"dataTables_paginate\">\n                <ul class=\"vtl-paging-pagination-ul vtl-pagination p-0 m-0\">\n                  <li class=\"\n                      vtl-paging-pagination-page-li\n                      vtl-paging-pagination-page-li-first\n                      page-item\n                    \" :class=\"{ disabled: setting.page <= 1 }\">\n                    <a class=\"\n                        vtl-paging-pagination-page-link\n                        vtl-paging-pagination-page-link-first\n                        page-link cursor-pointer\n                      \" href=\"javascript:void(0)\" aria-label=\"Previous\" @click.prevent=\"setting.page = 1\">\n                      <span aria-hidden=\"true\">\n                        <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"16\" height=\"16\" fill=\"currentColor\"\n                          class=\"bi bi-skip-start-fill\" viewBox=\"0 0 16 16\">\n                          <path\n                            d=\"M4 4a.5.5 0 0 1 1 0v3.248l6.267-3.636c.54-.313 1.232.066 1.232.696v7.384c0 .63-.692 1.01-1.232.697L5 8.753V12a.5.5 0 0 1-1 0V4z\" />\n                        </svg>\n                      </span>\n                      <span class=\"sr-only\">First</span>\n                    </a>\n                  </li>\n                  <li class=\"\n                      vtl-paging-pagination-page-li\n                      vtl-paging-pagination-page-li-prev\n                      page-item\n                    \" :class=\"{ disabled: setting.page <= 1 }\">\n                    <a class=\"\n                        vtl-paging-pagination-page-link\n                        vtl-paging-pagination-page-link-prev\n                        page-link cursor-pointer\n                      \" href=\"javascript:void(0)\" aria-label=\"Previous\" @click.prevent=\"prevPage\">\n                      <span aria-hidden=\"true\">\n                        <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"16\" height=\"16\" fill=\"currentColor\"\n                          class=\"bi bi-caret-left-fill\" viewBox=\"0 0 16 16\">\n                          <path\n                            d=\"m3.86 8.753 5.482 4.796c.646.566 1.658.106 1.658-.753V3.204a1 1 0 0 0-1.659-.753l-5.48 4.796a1 1 0 0 0 0 1.506z\" />\n                        </svg>\n                      </span>\n                      <span class=\"sr-only\">Prev</span>\n                    </a>\n                  </li>\n                  <li class=\"\n                      vtl-paging-pagination-page-li\n                      vtl-paging-pagination-page-li-number\n                      page-item\n                    \" v-for=\"n in setting.paging\" :key=\"n\" :class=\"{ disabled: setting.page === n }\">\n                    <a class=\"\n                        vtl-paging-pagination-page-link\n                        vtl-paging-pagination-page-link-number\n                        page-link cursor-pointer\n                      \" href=\"javascript:void(0)\" @click.prevent=\"movePage(n)\">{{ n }}</a>\n                  </li>\n                  <li class=\"\n                      vtl-paging-pagination-page-li\n                      vtl-paging-pagination-page-li-next\n                      page-item\n                    \" :class=\"{ disabled: setting.page >= setting.maxPage }\">\n                    <a class=\"\n                        vtl-paging-pagination-page-link\n                        vtl-paging-pagination-page-link-next\n                        page-link cursor-pointer\n                      \" href=\"javascript:void(0)\" aria-label=\"Next\" @click.prevent=\"nextPage\">\n                      <span aria-hidden=\"true\"><svg xmlns=\"http://www.w3.org/2000/svg\" width=\"16\" height=\"16\"\n                          fill=\"currentColor\" class=\"bi bi-caret-right-fill\" viewBox=\"0 0 16 16\">\n                          <path\n                            d=\"m12.14 8.753-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z\" />\n                        </svg>\n                      </span>\n                      <span class=\"sr-only\">Next</span>\n                    </a>\n                  </li>\n                  <li class=\"\n                      vtl-paging-pagination-page-li\n                      vtl-paging-pagination-page-li-last\n                      page-item\n                    \" :class=\"{ disabled: setting.page >= setting.maxPage }\">\n                    <a class=\"\n                        vtl-paging-pagination-page-link\n                        vtl-paging-pagination-page-link-last\n                        page-link cursor-pointer\n                      \" href=\"javascript:void(0)\" aria-label=\"Next\" @click.prevent=\"setting.page = setting.maxPage\">\n                      <span aria-hidden=\"true\">\n                        <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"16\" height=\"16\" fill=\"currentColor\"\n                          class=\"bi bi-skip-end-fill\" viewBox=\"0 0 16 16\">\n                          <path\n                            d=\"M12.5 4a.5.5 0 0 0-1 0v3.248L5.233 3.612C4.693 3.3 4 3.678 4 4.308v7.384c0 .63.692 1.01 1.233.697L11.5 8.753V12a.5.5 0 0 0 1 0V4z\" />\n                        </svg>\n                      </span>\n                      <span class=\"sr-only\">Last</span>\n                    </a>\n                  </li>\n                </ul>\n              </div>\n            </div>\n          </div>\n        </template>\n      </div>\n      <div class=\"vtl-row\" v-else>\n        <div class=\"vtl-empty-msg col-12 text-center\">\n          {{ messages.noDataAvailable }}\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport {\n  defineComponent,\n  ref,\n  reactive,\n  computed,\n  watch,\n  onBeforeUpdate,\n  nextTick,\n  onMounted,\n} from \"vue\";\n\nexport default defineComponent({\n  name: \"my-table\",\n  emits: [\n    \"return-checked-rows\",\n    \"do-search\",\n    \"is-finished\",\n    \"get-now-page\",\n    \"row-clicked\",\n    \"row-toggled\",\n  ],\n  props: {\n    //  (is data loading)\n    isLoading: {\n      type: Boolean,\n      require: true,\n    },\n    //  (Whether to perform a re-query)\n    isReSearch: {\n      type: Boolean,\n      require: true,\n    },\n    // Checkbox (Presence of Checkbox)\n    hasCheckbox: {\n      type: Boolean,\n      default: false,\n    },\n    // Checkbox (Returns data type for checked of Checkbox)\n    checkedReturnType: {\n      type: String,\n      default: \"row\",\n    },\n    //  (title)\n    title: {\n      type: String,\n      default: \"\",\n    },\n    //  (Fixed first column's position)\n    isFixedFirstColumn: {\n      type: Boolean,\n      default: false,\n    },\n    //  (Field)\n    columns: {\n      type: Array,\n      default: () => {\n        return [];\n      },\n    },\n    //  (data)\n    rows: {\n      type: Array,\n      default: () => {\n        return [];\n      },\n    },\n    //  (data row classes)\n    rowClasses: {\n      type: [Array, Function],\n      default: () => {\n        return [];\n      },\n    },\n    //  (Display the number of items on one page)\n    pageSize: {\n      type: Number,\n      default: 10,\n    },\n    //  (Total number of transactions)\n    total: {\n      type: Number,\n      default: 100,\n    },\n    //  (Current page number)\n    page: {\n      type: Number,\n      default: 1,\n    },\n    //  (Sort condition)\n    sortable: {\n      type: Object,\n      default: () => {\n        return {\n          order: \"id\",\n          sort: \"asc\",\n        };\n      },\n    },\n    //  (Display text)\n    messages: {\n      type: Object,\n      default: () => {\n        return {\n          pagingInfo: \"Showing {0}-{1} of {2}\",\n          pageSizeChangeLabel: \"Row count:\",\n          gotoPageLabel: \"Go to page:\",\n          noDataAvailable: \"No data\",\n        };\n      },\n    },\n    //  (Static mode(no refresh server data))\n    isStaticMode: {\n      type: Boolean,\n      default: false,\n    },\n    //  (V-slot mode)\n    isSlotMode: {\n      type: Boolean,\n      default: false,\n    },\n    //  (Hide paging)\n    isHidePaging: {\n      type: Boolean,\n      default: false,\n    },\n    //  (Dropdown of Display the number of items on one page)\n    pageOptions: {\n      type: Array,\n      default: () => [\n        {\n          value: 10,\n          text: 10,\n        },\n        {\n          value: 25,\n          text: 25,\n        },\n        {\n          value: 50,\n          text: 50,\n        },\n      ],\n    },\n\n    //  (Key of grouping)\n    groupingKey: {\n      type: String,\n      default: \"\",\n    },\n    //  (Has hide group rows toggle)\n    hasGroupToggle: {\n      type: Boolean,\n      default: false,\n    },\n    //  (Customize grouping title)\n    groupingDisplay: {\n      type: Function,\n      default: null,\n    },\n    //  (Table's max height)\n    maxHeight: {\n      default: \"auto\",\n    },\n    //  (Grouping collapsed on start)\n    startCollapsed: {\n      type: Boolean,\n      default: false,\n    },\n    //  (Keep collapsed status after refresh)\n    isKeepCollapsed: {\n      type: Boolean,\n      default: false,\n    },\n  },\n  setup(props, { emit, slots }) {\n    let localTable = ref(null);\n\n    //  (Validate dropdown's values have page-size value or not)\n    let defaultPageSize =\n      props.pageOptions.length > 0\n        ? ref(props.pageOptions[0].value)\n        : ref(props.pageSize);\n    if (props.pageOptions.length > 0) {\n      props.pageOptions.forEach((v) => {\n        if (\n          Object.prototype.hasOwnProperty.call(v, \"value\") &&\n          Object.prototype.hasOwnProperty.call(v, \"text\") &&\n          props.pageSize == v.value\n        ) {\n          defaultPageSize.value = v.value;\n        }\n      });\n    }\n\n    //  (Internal set value for components)\n    const setting = reactive({\n      // Slot (Enable slot mode)\n      isSlotMode: props.isSlotMode,\n      //  (Whether to select all)\n      isCheckAll: false,\n      //  (Hide paging)\n      isHidePaging: props.isHidePaging,\n      // KEY (KEY field name)\n      keyColumn: computed(() => {\n        let key = \"\";\n        Object.assign(props.columns).forEach((col) => {\n          if (col.isKey) {\n            key = col.field;\n          }\n        });\n        return key;\n      }),\n      //  (current page number)\n      page: props.page,\n      //  (Display count per page)\n      pageSize: defaultPageSize.value,\n      //  (Maximum number of pages)\n      maxPage: computed(() => {\n        if (props.total <= 0) {\n          return 0;\n        }\n        let maxPage = Math.floor(props.total / setting.pageSize);\n        let mod = props.total % setting.pageSize;\n        if (mod > 0) {\n          maxPage++;\n        }\n        return maxPage;\n      }),\n      //  (The starting value of the page number)\n      offset: computed(() => {\n        return (setting.page - 1) * setting.pageSize + 1;\n      }),\n      //  (Maximum number of pages0\n      limit: computed(() => {\n        let limit = setting.page * setting.pageSize;\n        return props.total >= limit ? limit : props.total;\n      }),\n      //  (Paging array)\n      paging: computed(() => {\n        let startPage = setting.page - 2 <= 0 ? 1 : setting.page - 2;\n        if (setting.maxPage - setting.page <= 2) {\n          startPage = setting.maxPage - 4;\n        }\n        startPage = startPage <= 0 ? 1 : startPage;\n        let pages = [];\n        for (let i = startPage; i <= setting.maxPage; i++) {\n          if (pages.length < 5) {\n            pages.push(i);\n          }\n        }\n        return pages;\n      }),\n      //  (Sortable for local)\n      order: props.sortable.order,\n      sort: props.sortable.sort,\n      pageOptions: props.pageOptions,\n    });\n\n    //  (Checked rows)\n    const isChecked = ref([]);\n\n    //  (Data rows for local)\n    const localRows = computed(() => {\n      let rows = props.rows;\n      // refs https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/Collator/compare\n      var collator = new Intl.Collator(undefined, {\n        numeric: true,\n        sensitivity: \"base\",\n      });\n      let sortOrder = setting.sort === \"desc\" ? -1 : 1;\n      rows.sort(function (a, b) {\n        return collator.compare(a[setting.order], b[setting.order]) * sortOrder;\n      });\n\n      // return sorted and offset rows\n\n      let result = null;\n      if (props.groupingKey) {\n        // If have set grouping-key create group temp data\n        let tmp = {};\n        rows.forEach((v) => {\n          if (!tmp[v[props.groupingKey]]) {\n            tmp[v[props.groupingKey]] = [];\n          }\n          tmp[v[props.groupingKey]].push(v);\n        });\n\n        result = {};\n        for (let index = setting.offset - 1; index < setting.limit; index++) {\n          result[rows[index][props.groupingKey]] = tmp[rows[index][props.groupingKey]];\n        }\n      } else {\n        result = [];\n        for (let index = setting.offset - 1; index < setting.limit; index++) {\n          result.push(rows[index]);\n        }\n      }\n\n      nextTick(function () {\n        // \n        callIsFinished();\n      });\n\n      return result;\n    });\n\n    ////////////////////////////\n    //\n    //  Checkbox \n    //  (Checkbox related operations)\n    //\n\n    // Checkbox (Define Checkbox reference)\n    const rowCheckbox = ref([]);\n\n    /**\n     *  (Execute before re-rendering)\n     */\n    onBeforeUpdate(() => {\n      //  (Clear all values before each update)\n      rowCheckbox.value = [];\n    });\n\n    /**\n     * Checkbox (Check all checkboxes for monitoring)\n     */\n    watch(\n      () => setting.isCheckAll,\n      (state) => {\n        if (props.hasCheckbox) {\n          isChecked.value = [];\n          if (state) {\n            let tmpRows = (props.isStaticMode) ? props.rows.slice((setting.offset - 1), setting.limit) : props.rows;\n            if (props.checkedReturnType == \"row\") {\n              isChecked.value = tmpRows;\n            } else {\n              tmpRows.forEach((val) => {\n                isChecked.value.push(val[setting.keyColumn]);\n              });\n            }\n          }\n          rowCheckbox.value.forEach((val) => {\n            if (val) {\n              val.checked = state;\n            }\n          });\n          //  (Return the selected data on the screen)\n          emit(\"return-checked-rows\", isChecked.value);\n        }\n      }\n    );\n\n    /**\n     * Checkbox (hasCeckbox props for monitoring)\n     */\n    watch(\n      () => props.hasCheckbox,\n      (v) => {\n        if (!v) {\n          setting.isCheckAll = false;\n        }\n      }\n    );\n\n    // z-index of Fixed Columns cause click event being eaten by parent element, so I need to do this instead (Vincent)\n    const clickChild = (event) => {\n      // console.log(event.target.firstChild); // test\n      if (props.isFixedFirstColumn && event.target.firstElementChild !== null) {\n        event.target.firstElementChild.click();\n      } // if\n    } // clickChild\n\n    /**\n     * Checkbox (Checkbox click event)\n     */\n    const checked = (row, event) => {\n      event.stopPropagation();\n      let checkboxValue = row[setting.keyColumn];\n      if (props.checkedReturnType == \"row\") {\n        checkboxValue = row;\n      }\n      if (event.target.checked) {\n        isChecked.value.push(checkboxValue);\n      } else {\n        const index = isChecked.value.indexOf(checkboxValue);\n        if (index >= 0) {\n          isChecked.value.splice(index, 1);\n        }\n      }\n      if (isChecked.value.length == props.rows.length) {\n        setting.isCheckAll = true;\n      } else {\n        //  (Return the selected data on the screen)\n        emit(\"return-checked-rows\", isChecked.value);\n      }\n    };\n\n    /**\n     *  (Clear all selected data on the screen)\n     */\n    const clearChecked = () => {\n      isChecked.value = [];\n      rowCheckbox.value.forEach((val) => {\n        if (val && val.checked) {\n          val.checked = false;\n        }\n      });\n      //  (Return the selected data on the screen)\n      emit(\"return-checked-rows\", isChecked.value);\n    };\n\n    ////////////////////////////\n    //\n    //   \n    //  (Sorting, page change, etc. related operations)\n    //\n\n    /**\n     *  (Call execution sequencing)\n     */\n    const doSort = (order) => {\n      let sort = \"asc\";\n      if (order == setting.order) {\n        //  (When sorting items)\n        if (setting.sort == \"asc\") {\n          sort = \"desc\";\n        }\n      }\n      let offset = (setting.page - 1) * setting.pageSize;\n      let limit = setting.pageSize;\n      setting.order = order;\n      setting.sort = sort;\n      emit(\"do-search\", offset, limit, order, sort);\n\n      //  (Clear the selected data on the screen)\n      if (setting.isCheckAll) {\n        //  (It will be cleared when you cancel all selections)\n        setting.isCheckAll = false;\n      } else {\n        if (props.hasCheckbox) {\n          clearChecked();\n        }\n      }\n    };\n\n    /**\n     *  (Switch page number)\n     *\n     * @param page      number      (New page number)\n     * @param prevPage  number    (Current page number)\n     */\n    const changePage = (page, prevPage) => {\n      setting.isCheckAll = false;\n      if (props.hasCheckbox) {\n        isChecked.value = [];\n      }\n      let order = setting.order;\n      let sort = setting.sort;\n      let offset = (page - 1) * setting.pageSize;\n      let limit = setting.pageSize;\n      if (!props.isReSearch || page > 1 || page == prevPage) {\n        //  (Call query will only be executed if the page number is changed without re-query)\n        emit(\"do-search\", offset, limit, order, sort);\n      }\n    };\n    //  (Monitor page switching)\n    watch(() => setting.page, changePage);\n    //  (Monitor manual page switching)\n    watch(\n      () => props.page,\n      (val) => {\n        if (val <= 1) {\n          setting.page = 1;\n          emit(\"get-now-page\", setting.page);\n        } else if (val >= setting.maxPage) {\n          setting.page = setting.maxPage;\n          emit(\"get-now-page\", setting.page);\n        } else {\n          setting.page = val;\n        }\n      }\n    );\n\n    /**\n     *  (Switch display number)\n     */\n    const changePageSize = () => {\n      if (setting.page === 1) {\n        //  changePage()\n        changePage(setting.page, setting.page);\n      } else {\n        // , changePage()\n        setting.page = 1;\n        setting.isCheckAll = false;\n      }\n    };\n    //  (Monitor display number switch from component)\n    watch(() => setting.pageSize, changePageSize);\n    // Prop (Monitor display number switch from prop)\n    watch(\n      () => props.pageSize,\n      (newPageSize) => {\n        setting.pageSize = newPageSize;\n      }\n    );\n\n    /**\n     *  (Previous page)\n     */\n    const prevPage = () => {\n      if (setting.page == 1) {\n        //  (If it is the first page, it will not be executed)\n        return false;\n      }\n      setting.page--;\n    };\n\n    /**\n     *  (Move to the specified number of pages)\n     */\n    const movePage = (page) => {\n      setting.page = page;\n    };\n\n    /**\n     *  (Next page)\n     */\n    const nextPage = () => {\n      if (setting.page >= setting.maxPage) {\n        //  (If it is equal to or greater than the maximum number of pages, no execution)\n        return false;\n      }\n      setting.page++;\n    };\n\n    //  (Monitoring data changes)\n    watch(\n      () => props.rows,\n      () => {\n        if (props.isReSearch || props.isStaticMode) {\n          setting.page = 1;\n        }\n        nextTick(function () {\n          //  (Return the private components after the data is rendered)\n          if (!props.isStaticMode) {\n            callIsFinished();\n          }\n        });\n      }\n    );\n\n    const stringFormat = (template, ...args) => {\n      return template.replace(/{(\\d+)}/g, function (match, number) {\n        return typeof args[number] != \"undefined\" ? args[number] : match;\n      });\n    };\n\n    // Call is-finished Method\n    const callIsFinished = () => {\n      if (localTable.value) {\n        let localElement =\n          localTable.value.getElementsByClassName(\"is-rows-el\");\n        emit(\"is-finished\", localElement);\n      }\n      emit(\"get-now-page\", setting.page);\n    };\n\n    // Toggle button elements\n    const toggleButtonRefs = ref({});\n    // Grouping rows\n    const groupingRowsRefs = ref({});\n    // Saved toggle status\n    const groupingToggleStatus = ref({});\n\n    // Data rows for grouping (Default-mode only)\n    const groupingRows = computed(() => {\n      let result = {};\n      props.rows.forEach((v) => {\n        if (!result[v[props.groupingKey]]) {\n          result[v[props.groupingKey]] = [];\n        }\n        result[v[props.groupingKey]].push(v);\n      });\n\n      nextTick(function () {\n        if (props.startCollapsed || props.isKeepCollapsed) {\n          for (const [groupIndex, el] of Object.entries(toggleButtonRefs.value)) {\n            if (el) {\n              let isOpen = !props.startCollapsed;\n              if (\n                props.isKeepCollapsed &&\n                groupingToggleStatus.value[groupIndex] !== undefined\n              ) {\n                isOpen = !groupingToggleStatus.value[groupIndex];\n              }\n              if (\n                (isOpen && el.parentElement.classList.contains(\"rotated-90\")) ||\n                (!isOpen && !el.parentElement.classList.contains(\"rotated-90\"))\n              ) {\n                el.parentElement.classList.toggle(\"rotated-90\");\n              }\n              if (!isOpen) {\n                groupingRowsRefs.value[groupIndex].forEach((element) => {\n                  if (element) {\n                    element.classList.add(\"hidden\");\n                  }\n                });\n              }\n            }\n          }\n        }\n        callIsFinished();\n      });\n\n      return result;\n    });\n\n    /**\n     * Toggle Group rows\n     *\n     * @param {String} groupIndex\n     */\n    const toggleGroup = (groupIndex) => {\n      let targetEl = toggleButtonRefs.value[groupIndex];\n      if (targetEl) {\n        targetEl.parentElement.classList.toggle(\"rotated-90\");\n        const isClose = targetEl.parentElement.classList.contains(\"rotated-90\");\n        groupingRowsRefs.value[groupIndex].forEach((element) => {\n          if (element) {\n            if (isClose) {\n              element.classList.add(\"hidden\");\n            } else {\n              element.classList.remove(\"hidden\");\n            }\n          }\n        });\n        groupingToggleStatus.value[groupIndex] = isClose;\n        emit(\"row-toggled\", groupingRows.value[groupIndex], isClose);\n      }\n    };\n\n    /**\n     *  (Mounted Event)\n     */\n    onMounted(() => {\n      nextTick(() => {\n        if (props.rows.length > 0) {\n          callIsFinished();\n        }\n      });\n    });\n\n    return {\n      slots,\n      localTable,\n      localRows,\n      setting,\n      rowCheckbox,\n      clickChild,\n      checked,\n      clearChecked,\n      doSort,\n      prevPage,\n      movePage,\n      nextPage,\n      stringFormat,\n      toggleButtonRefs,\n  