ExcelClick,\n        };\n    }, // setup\n});\n</script>\n<style scoped>\n::v-deep(.vtl-table .vtl-thead .vtl-thead-th) {\n    color: #fff;\n    background-color: #196241;\n    border-color: #196241;\n}\n</style>"],"sourceRoot":""}]);
