onSendClick(false, $event)\" type=\"button\" class=\"btn btn-lg btn-success\"\n                        style=\"border-radius: 5px;\">\n                        {{ $t('monthlyPRpageLang.confirm_pr') }}\n                    </button>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { defineComponent, reactive, ref, computed } from \"vue\";\nimport {\n    getCurrentInstance,\n    onBeforeMount,\n    onMounted,\n    watch,\n} from \"@vue/runtime-core\";\nimport * as XLSX from 'xlsx';\nimport TableLite from \"./TableLite.vue\";\nimport useMonthlyPRSearch from \"../../composables/MonthlyPRSearch.ts\";\nimport useSxbSearch from \"../../composables/SxbSearch.ts\";\nexport default defineComponent({\n    name: \"App\",\n    components: { TableLite },\n    setup() {\n        const app = getCurrentInstance(); // get the current instance\n        let thisHtmlLang = document\n            .getElementsByTagName(\"HTML\")[0]\n            .getAttribute(\"lang\");\n        // get the current locale from html tag\n        app.appContext.config.globalProperties.$lang.setLocale(thisHtmlLang); // set the current locale to vue package\n\n        const { mats, Currency, getMats_Buylist, getMats_nonMonthly, sendBuylistMail, submitBuylist, getCurrency } = useMonthlyPRSearch(); // axios get the mats data\n        const { mats_SXB, getMats } = useSxbSearch(); // axios get the mats data\n\n        onBeforeMount(getCurrency);\n\n        const selectedValue2 = ref('USD');\n        let isInvalid = ref(true); // validation\n\n        let isInvalid_DB = ref(false); // add to DB validation\n        let validation_err_msg = ref(app.appContext.config.globalProperties.$t(\"validation.required\"));\n        let checkedRows = [];\n        let uploadToDBReady = ref(false); // validation\n        let needConfirm = ref(false); // try to prevent redundant PR on the same PN\n        let needConfirm2 = ref(false); // try to prevent redundant PR on the selected PN\n\n        const MPSData = ref(null);\n        const nonMPSData = ref(null);\n        const combinedData = ref(null);\n        let nonMPS_PN_Array = [];\n        let MPS_PN_Array = [];\n        let MPS_90PN_Array = [];\n\n        const searchTerm = ref(\"\"); // Search text\n        const searchTerm2 = ref(\"\"); // Search text for modal table\n        const modalTitle1 = ref(\"\");\n        const modalTitle2 = ref(\"\");\n        let showFooter = ref(true);\n\n\n        // pour the data in\n        const data = reactive([]);\n        const data2 = reactive([]);\n\n        const triggerModal = async () => {\n            $(\"body\").loadingModal({\n                text: \"Loading...\",\n                animation: \"circle\",\n            });\n\n            return new Promise((resolve, reject) => {\n                resolve();\n            });\n        } // triggerModal\n\n        const deleteRow = () => {\n            if (checkedRows.length == 0) {\n                notyf.open({\n                    type: \"warning\",\n                    message: app.appContext.config.globalProperties.$t(\"basicInfoLang.nodata\"),\n                    duration: 3000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n\n                return;\n            } // if\n\n            for (let i = 0; i < checkedRows.length; i++) {\n                let indexOfObject = data.findIndex(object => {\n                    return parseInt(object.id) === parseInt(checkedRows[i].id);\n                });\n\n                if (indexOfObject != -1) {\n                    data.splice(indexOfObject, 1);\n                } // if\n            } // for\n\n            document.querySelectorAll('.vtl-tbody-checkbox').forEach(el => el.checked = false);\n            if (document.querySelector(\".vtl-thead-checkbox\").checked) {\n                document.querySelector(\".vtl-thead-checkbox\").click();\n            } // if\n            checkedRows = [];\n\n        } // deleteRowv\n\n        const OutputExcelClick = async () => {\n            await triggerModal();\n\n            // get today's date for filename\n            let today = new Date();\n            let dd = String(today.getDate()).padStart(2, '0');\n            let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!\n            let yyyy = today.getFullYear();\n            today = yyyy + \"_\" + mm + '_' + dd;\n\n            let rows = Array();\n\n            for (let i = 0; i < data.length; i++) {\n                let tempObj = new Object;\n                tempObj. = data[i].;\n                tempObj. = data[i].;\n                tempObj. = data[i].;\n                tempObj. = data[i].;\n                tempObj.1 = data[i].;\n                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\");\n                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\");\n                tempObj. = parseInt(data[i].).toLocaleString(\"en-US\");\n                tempObj. = parseInt(data[i].).toLocaleString(\"en-US\");\n                tempObj. = parseInt(data[i].).toLocaleString(\"en-US\");\n                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\");\n                tempObj.2 = data[i].;\n                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\");\n                tempObj.MOQ = data[i].MOQ;\n                rows.push(tempObj);\n            } // for\n\n            const worksheet = XLSX.utils.json_to_sheet(rows);\n\n            // change header name\n            XLSX.utils.sheet_add_aoa(worksheet,\n                [[\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.isn\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.pName\"),\n                    app.appContext.config.globalProperties.$t(\"inboundpageLang.format\"),\n                    app.appContext.config.globalProperties.$t(\"basicInfoLang.price\"),\n                    app.appContext.config.globalProperties.$t(\"basicInfoLang.money\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.nowneed\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.nextneed\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.nowstock\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.transit\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.buyamount\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.buyprice\"),\n                    app.appContext.config.globalProperties.$t(\"basicInfoLang.money\"),\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.buyprice\") + \"(USD)\",\n                    app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.moq\"),\n                ]],\n                { origin: \"A1\" });\n\n            const workbook = XLSX.utils.book_new();\n            XLSX.utils.book_append_sheet(workbook, worksheet, app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.PR\"));\n            XLSX.writeFile(workbook,\n                app.appContext.config.globalProperties.$t(\n                    \"monthlyPRpageLang.PR\"\n                ) + \"_\" + today + \".xlsx\", { compression: true });\n\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n        } // OutputExcelClick\n\n        const onSendClick = async (selectedOnly) => {\n            await triggerModal();\n            isInvalid_DB.value = false;\n            let rowsCount = 0;\n            let hasError = false;\n\n            if (selectedOnly || needConfirm2.value) {\n                if (checkedRows.length <= 0) {\n                    notyf.open({\n                        type: \"warning\",\n                        message: app.appContext.config.globalProperties.$t(\"basicInfoLang.nodata\"),\n                        duration: 3000, //miliseconds, use 0 for infinite duration\n                        ripple: true,\n                        dismissible: true,\n                        position: {\n                            x: \"right\",\n                            y: \"bottom\",\n                        },\n                    });\n\n                    $(\"body\").loadingModal(\"hide\");\n                    $(\"body\").loadingModal(\"destroy\");\n                    return;\n                } // if\n            } else {\n                if (data.length <= 0) {\n                    notyf.open({\n                        type: \"warning\",\n                        message: app.appContext.config.globalProperties.$t(\"basicInfoLang.nodata\"),\n                        duration: 3000, //miliseconds, use 0 for infinite duration\n                        ripple: true,\n                        dismissible: true,\n                        position: {\n                            x: \"right\",\n                            y: \"bottom\",\n                        },\n                    });\n\n                    $(\"body\").loadingModal(\"hide\");\n                    $(\"body\").loadingModal(\"destroy\");\n                    return;\n                } // if\n            } // if else\n\n            // ----------------------------------------------\n            // actually updating database now\n            let number = [];\n            let pName = [];\n            let spec = [];\n            let unit_price = [];\n            let nowNeed = [];\n            let nextNeed = [];\n            let stock = [];\n            let in_transit = [];\n            let req_amount = [];\n            let total_price_default_currency = [];\n            let total_price_default_currency_name = [];\n            let total_price_other_currency = [];\n            let moq = [];\n\n            let tempData;\n            if (selectedOnly || needConfirm2.value) {\n                tempData = checkedRows;\n            } else {\n                tempData = data;\n            } // else\n\n            tempData.sort((a, b) => b. - a.);\n            for (let i = 0; i < tempData.length; i++) {\n                if (parseInt(tempData[i].) > 0) { // send only the one that has value\n                    number.push(tempData[i].);\n                    pName.push(tempData[i].);\n                    spec.push(tempData[i].);\n                    unit_price.push(tempData[i].);\n                    nowNeed.push(parseFloat(tempData[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\"));\n                    nextNeed.push(parseFloat(tempData[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\"));\n                    stock.push(parseInt(tempData[i].).toLocaleString(\"en-US\"));\n                    in_transit.push(parseInt(tempData[i].).toLocaleString(\"en-US\"));\n                    req_amount.push(parseInt(tempData[i].).toLocaleString(\"en-US\"));\n                    total_price_default_currency.push(parseFloat(tempData[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\"));\n                    total_price_default_currency_name.push(tempData[i].);\n                    total_price_other_currency.push(parseFloat(tempData[i].).toFixed(2).replace(/\\B(?<!\\.\\d*)(?=(\\d{3})+(?!\\d))/g, \",\"));\n                    moq.push(tempData[i].MOQ);\n                } // if\n                else {\n                    hasError = true;\n                } // else\n            } // for\n\n            if (number.length <= 0) {\n                isInvalid_DB.value = true;\n                validation_err_msg.value = app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.no_req_amount_to_submit\");\n\n                notyf.open({\n                    type: \"warning\",\n                    message: app.appContext.config.globalProperties.$t(\"basicInfoLang.nodata\"),\n                    duration: 3000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n\n                $(\"body\").loadingModal(\"hide\");\n                $(\"body\").loadingModal(\"destroy\");\n                return;\n            } // if\n            else if (hasError) {\n                isInvalid_DB.value = true;\n                validation_err_msg.value = app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.some_data_has_no_amount\");\n                notyf.open({\n                    type: \"warning\",\n                    message: app.appContext.config.globalProperties.$t(\"monthlyPRpageLang.some_data_has_no_amount\"),\n                    duration: 5000, //miliseconds, use 0 for infinite duration\n                    ripple: true,\n                    dismissible: true,\n                    position: {\n                        x: \"right\",\n                        y: \"bottom\",\n                    },\n                });\n            } // else if\n\n            // console.log(total_price_other_currency); // test\n            // return;\n            let start = Date.now();\n            let result = await sendBuylistMail(\n                number, pName, spec, unit_price,\n                nowNeed, nextNeed, stock, in_transit, req_amount,\n                total_price_default_currency, total_price_default_currency_name, total_price_other_currency, moq\n            );\n\n            let timeTaken = Date.now() - start;\n            console.log(\"Total time taken : \" + timeTaken + \" milliseconds\");\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n\n            if (selectedOnly || needConfirm2.value) {\n                let checkedPN = checkedRows.map(obj => obj.);\n                // Remove items from MPS_PN_Array if not in checkedPN\n                for (let i = MPS_PN_Array.length - 1; i >= 0; i--) {\n                    if (!checkedPN.includes(MPS_PN_Array[i])) {\n                        MPS_PN_Array.splice(i, 1);\n                        MPS_90PN_Array.splice(i, 1);\n                    } // if\n                } // for\n\n                // Remove items from nonMPS_PN_Array if not in checkedPN\n                for (let i = nonMPS_PN_Array.length - 1; i >= 0; i--) {\n                    if (!checkedPN.includes(nonMPS_PN_Array[i])) {\n                        nonMPS_PN_Array.splice(i, 1);\n                    } // if\n                } // for\n            } // if\n            // ------- quick test --------\n            // result = await submitBuylist(\n            //     number, pName, spec, unit_price,\n            //     nowNeed, nextNeed, stock, in_transit, req_amount,\n            //     total_price_default_currency, total_price_default_currency_name,\n            //     total_price_other_currency, moq, nonMPS_PN_Array, MPS_90PN_Array, MPS_PN_Array\n            // );\n            // result = \"failed\";\n            // ------- ---------- --------\n\n            if (result === \"success\") {\n                result = await submitBuylist(\n                    number, pName, spec, unit_price,\n                    nowNeed, nextNeed, stock, in_transit, req_amount,\n                    total_price_default_currency, total_price_default_currency_name,\n                    total_price_other_currency, moq, nonMPS_PN_Array, MPS_90PN_Array, MPS_PN_Array\n                );\n\n                if (result === \"success\") {\n                    uploadToDBReady.value = false;\n          