<template>
    <div class="card">
        <div class="card-header ">
            <h3>{{ $t("monthlyPRpageLang.PR") }}</h3>
        </div>
        <div class="card-body">
            <div class="row justify-content-between">
                <div class="row col col-auto">
                    <div class="col col-auto">
                        <label for="pnInput" class="col-form-label">{{ $t("basicInfoLang.quicksearch") }} :</label>
                    </div>
                    <div class="col col-6 p-0 m-0">
                        <input id="pnInput" class="text-center form-control form-control-lg"
                            v-bind:placeholder="$t('monthlyPRpageLang.enterisn_or_descr')" v-model="searchTerm" />
                    </div>
                </div>
                <div class="col col-auto">
                    <button id="delete" name="delete" class="col col-auto btn btn-lg btn-danger" @click="deleteRow">
                        <i class="bi bi-trash3-fill fs-4"></i>
                    </button>
                    &nbsp;
                    <button id="download" name="download" class="col col-auto btn btn-lg btn-success"
                        :value="$t('monthlyPRpageLang.download')" @click="OutputExcelClick">
                        <i class="bi bi-file-earmark-arrow-down-fill fs-4"></i>
                    </button>
                </div>
            </div>
            <div class="w-100" style="height: 1ch"></div><!-- </div>breaks cols to a new line-->
            <div class="row justify-content-between">
                <span v-if="isInvalid_DB" class="col col-auto text-danger m-0 p-0" role="alert">
                    <strong>{{ validation_err_msg }}</strong>
                </span>
                <span v-else class="col col-auto text-danger m-0 p-0" role="alert">
                    <strong></strong>
                </span>
                <span class="col col-auto text-sm text-danger m-0 p-0">
                    {{ $t('monthlyPRpageLang.shallow_delete') }}
                </span>
            </div>
            <table-lite id="searchTable" :is-fixed-first-column="true" :is-static-mode="true" :hasCheckbox="true"
                :isLoading="table.isLoading" :messages="table.messages" :columns="table.columns" :rows="table.rows"
                :total="table.totalRecordCount" :page-options="table.pageOptions" :sortable="table.sortable"
                @is-finished="table.isLoading = false" @return-checked-rows="updateCheckedRows"></table-lite>

            <div class="w-100" style="height: 2ch;"></div><!-- </div>breaks cols to a new line-->
            <div v-show="uploadToDBReady" class="row justify-content-center">
                <div v-if="needConfirm2" class="col col-auto m-0 p-0">
                    <button type="button" name="upload" data-bs-toggle="modal" data-bs-target="#sendPRConfirmTable"
                        aria-controls="sendPRConfirmTable"
                        class="col col-auto fs-3 text-center btn btn-lg btn-outline-info">
                        <i class="bi bi-envelope-check"></i>
                        {{ $t('monthlyPRpageLang.SendSelectedPRReview') }}
                    </button>
                </div>
                <div v-else class="col col-auto m-0 p-0">
                    <button @click="onSendClick(true, $event)" type="button" name="upload"
                        class="col col-auto fs-3 text-center btn btn-lg btn-outline-info">
                        <i class="bi bi-envelope-check"></i>
                        {{ $t('monthlyPRpageLang.SendSelectedPRReview') }}
                    </button>
                </div>
                <div class="w-100" style="height: 1ch"></div><!-- </div>breaks cols to a new line-->
                <div v-if="needConfirm" class="col col-auto m-0 p-0">
                    <button type="button" name="upload" data-bs-toggle="modal" data-bs-target="#sendPRConfirmTable"
                        aria-controls="sendPRConfirmTable" class="col col-auto fs-3 text-center btn btn-lg btn-info">
                        <i class="bi bi-envelope-at-fill"></i>
                        {{ $t('monthlyPRpageLang.SendPRReview') }}
                    </button>
                </div>
                <div v-else class="col col-auto m-0 p-0">
                    <button @click="onSendClick(false, $event)" type="button" name="upload"
                        class="col col-auto fs-3 text-center btn btn-lg btn-info">
                        <i class="bi bi-envelope-at-fill"></i>
                        {{ $t('monthlyPRpageLang.SendPRReview') }}
                    </button>
                </div>
            </div>
        </div>
    </div>

    <div class="modal fade" id="sendPRConfirmTable" tabindex="-1" aria-labelledby="sendPRConfirmTable"
        aria-hidden="true">
        <div class="modal-dialog modal-xl modal-dialog-centered modal-dialog-scrollable">
            <div class="modal-content">
                <div class="modal-header justify-content-center">
                    <div class="col col-10">
                        <span class="text-danger"><b>{{ modalTitle1 }}</b></span>
                        <div class="w-100" style="height: 0ch;"></div><!-- </div>breaks cols to a new line-->
                        <span class="text-danger"><b>{{ modalTitle2 }}</b></span>
                    </div>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    <div class="row justify-content-between">
                        <div class="row col col-auto">
                            <div class="col col-auto">
                                <label for="pnInput2" class="col-form-label">{{ $t("basicInfoLang.quicksearch") }}
                                    :</label>
                            </div>
                            <div class="col col-auto p-0 m-0">
                                <input id="pnInput2" class="text-center form-control form-control-lg"
                                    v-bind:placeholder="$t('monthlyPRpageLang.enterisn_or_descr')"
                                    v-model="searchTerm2" />
                            </div>
                        </div>
                    </div>
                    <div class="w-100" style="height: 1ch;"></div>
                    <!-- </div>breaks cols to a new line-->
                    <table-lite :is-static-mode="true" :isSlotMode="true" :hasCheckbox="false"
                        :messages="table2.messages" :columns="table2.columns" :rows="table2.rows"
                        :total="table2.totalRecordCount" :page-options="table2.pageOptions" :sortable="table2.sortable"
                        :is-fixed-first-column="false">
                    </table-lite>
                </div>
                <div v-if="showFooter" class="modal-footer justify-content-between">
                    <button type="button" class="btn btn-lg btn-danger" style="border-radius: 5px;"
                        data-bs-dismiss="modal" aria-label="Close">
                        {{ $t('templateWords.cancel') }}
                    </button>
                    <button @click="onSendClick(false, $event)" type="button" class="btn btn-lg btn-success"
                        style="border-radius: 5px;">
                        {{ $t('monthlyPRpageLang.confirm_pr') }}
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent, reactive, ref, computed } from "vue";
import {
    getCurrentInstance,
    onBeforeMount,
    onMounted,
    watch,
} from "@vue/runtime-core";
import * as XLSX from 'xlsx';
import TableLite from "./TableLite.vue";
import useMonthlyPRSearch from "../../composables/MonthlyPRSearch.ts";
import useSxbSearch from "../../composables/SxbSearch.ts";
export default defineComponent({
    name: "App",
    components: { TableLite },
    setup() {
        const app = getCurrentInstance(); // get the current instance
        let thisHtmlLang = document
            .getElementsByTagName("HTML")[0]
            .getAttribute("lang");
        // get the current locale from html tag
        app.appContext.config.globalProperties.$lang.setLocale(thisHtmlLang); // set the current locale to vue package

        const { mats, Currency, getMats_Buylist, getMats_nonMonthly, sendBuylistMail, submitBuylist, getCurrency } = useMonthlyPRSearch(); // axios get the mats data
        const { mats_SXB, getMats } = useSxbSearch(); // axios get the mats data

        onBeforeMount(getCurrency);

        const selectedValue2 = ref('USD');
        let isInvalid = ref(true); // validation

        let isInvalid_DB = ref(false); // add to DB validation
        let validation_err_msg = ref(app.appContext.config.globalProperties.$t("validation.required"));
        let checkedRows = [];
        let uploadToDBReady = ref(false); // validation
        let needConfirm = ref(false); // try to prevent redundant PR on the same PN
        let needConfirm2 = ref(false); // try to prevent redundant PR on the selected PN

        const MPSData = ref(null);
        const nonMPSData = ref(null);
        const combinedData = ref(null);
        let nonMPS_PN_Array = [];
        let MPS_PN_Array = [];
        let MPS_90PN_Array = [];

        const searchTerm = ref(""); // Search text
        const searchTerm2 = ref(""); // Search text for modal table
        const modalTitle1 = ref("");
        const modalTitle2 = ref("");
        let showFooter = ref(true);


        // pour the data in
        const data = reactive([]);
        const data2 = reactive([]);

        const triggerModal = async () => {
            $("body").loadingModal({
                text: "Loading...",
                animation: "circle",
            });

            return new Promise((resolve, reject) => {
                resolve();
            });
        } // triggerModal

        const deleteRow = () => {
            if (checkedRows.length == 0) {
                notyf.open({
                    type: "warning",
                    message: app.appContext.config.globalProperties.$t("basicInfoLang.nodata"),
                    duration: 3000, //miliseconds, use 0 for infinite duration
                    ripple: true,
                    dismissible: true,
                    position: {
                        x: "right",
                        y: "bottom",
                    },
                });

                return;
            } // if

            for (let i = 0; i < checkedRows.length; i++) {
                let indexOfObject = data.findIndex(object => {
                    return parseInt(object.id) === parseInt(checkedRows[i].id);
                });

                if (indexOfObject != -1) {
                    data.splice(indexOfObject, 1);
                } // if
            } // for

            document.querySelectorAll('.vtl-tbody-checkbox').forEach(el => el.checked = false);
            if (document.querySelector(".vtl-thead-checkbox").checked) {
                document.querySelector(".vtl-thead-checkbox").click();
            } // if
            checkedRows = [];

        } // deleteRowv

        const OutputExcelClick = async () => {
            await triggerModal();

            // get today's date for filename
            let today = new Date();
            let dd = String(today.getDate()).padStart(2, '0');
            let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
            let yyyy = today.getFullYear();
            today = yyyy + "_" + mm + '_' + dd;

            let rows = Array();

            for (let i = 0; i < data.length; i++) {
                let tempObj = new Object;
                tempObj. = data[i].;
                tempObj. = data[i].;
                tempObj. = data[i].;
                tempObj. = data[i].;
                tempObj.1 = data[i].;
                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
                tempObj. = parseInt(data[i].).toLocaleString("en-US");
                tempObj. = parseInt(data[i].).toLocaleString("en-US");
                tempObj. = parseInt(data[i].).toLocaleString("en-US");
                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
                tempObj.2 = data[i].;
                tempObj. = parseFloat(data[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
                tempObj.MOQ = data[i].MOQ;
                rows.push(tempObj);
            } // for

            const worksheet = XLSX.utils.json_to_sheet(rows);

            // change header name
            XLSX.utils.sheet_add_aoa(worksheet,
                [[
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.isn"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.pName"),
                    app.appContext.config.globalProperties.$t("inboundpageLang.format"),
                    app.appContext.config.globalProperties.$t("basicInfoLang.price"),
                    app.appContext.config.globalProperties.$t("basicInfoLang.money"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.nowneed"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.nextneed"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.nowstock"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.transit"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.buyamount"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.buyprice"),
                    app.appContext.config.globalProperties.$t("basicInfoLang.money"),
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.buyprice") + "(USD)",
                    app.appContext.config.globalProperties.$t("monthlyPRpageLang.moq"),
                ]],
                { origin: "A1" });

            const workbook = XLSX.utils.book_new();
            XLSX.utils.book_append_sheet(workbook, worksheet, app.appContext.config.globalProperties.$t("monthlyPRpageLang.PR"));
            XLSX.writeFile(workbook,
                app.appContext.config.globalProperties.$t(
                    "monthlyPRpageLang.PR"
                ) + "_" + today + ".xlsx", { compression: true });

            $("body").loadingModal("hide");
            $("body").loadingModal("destroy");
        } // OutputExcelClick

        const onSendClick = async (selectedOnly) => {
            await triggerModal();
            isInvalid_DB.value = false;
            let rowsCount = 0;
            let hasError = false;

            if (selectedOnly || needConfirm2.value) {
                if (checkedRows.length <= 0) {
                    notyf.open({
                        type: "warning",
                        message: app.appContext.config.globalProperties.$t("basicInfoLang.nodata"),
                        duration: 3000, //miliseconds, use 0 for infinite duration
                        ripple: true,
                        dismissible: true,
                        position: {
                            x: "right",
                            y: "bottom",
                        },
                    });

                    $("body").loadingModal("hide");
                    $("body").loadingModal("destroy");
                    return;
                } // if
            } else {
                if (data.length <= 0) {
                    notyf.open({
                        type: "warning",
                        message: app.appContext.config.globalProperties.$t("basicInfoLang.nodata"),
                        duration: 3000, //miliseconds, use 0 for infinite duration
                        ripple: true,
                        dismissible: true,
                        position: {
                            x: "right",
                            y: "bottom",
                        },
                    });

                    $("body").loadingModal("hide");
                    $("body").loadingModal("destroy");
                    return;
                } // if
            } // if else

            // ----------------------------------------------
            // actually updating database now
            let number = [];
            let pName = [];
            let spec = [];
            let unit_price = [];
            let nowNeed = [];
            let nextNeed = [];
            let stock = [];
            let in_transit = [];
            let req_amount = [];
            let total_price_default_currency = [];
            let total_price_default_currency_name = [];
            let total_price_other_currency = [];
            let moq = [];

            let tempData;
            if (selectedOnly || needConfirm2.value) {
                tempData = checkedRows;
            } else {
                tempData = data;
            } // else

            tempData.sort((a, b) => b. - a.);
            for (let i = 0; i < tempData.length; i++) {
                if (parseInt(tempData[i].) > 0) { // send only the one that has value
                    number.push(tempData[i].);
                    pName.push(tempData[i].);
                    spec.push(tempData[i].);
                    unit_price.push(tempData[i].);
                    nowNeed.push(parseFloat(tempData[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ","));
                    nextNeed.push(parseFloat(tempData[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ","));
                    stock.push(parseInt(tempData[i].).toLocaleString("en-US"));
                    in_transit.push(parseInt(tempData[i].).toLocaleString("en-US"));
                    req_amount.push(parseInt(tempData[i].).toLocaleString("en-US"));
                    total_price_default_currency.push(parseFloat(tempData[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ","));
                    total_price_default_currency_name.push(tempData[i].);
                    total_price_other_currency.push(parseFloat(tempData[i].).toFixed(2).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ","));
                    moq.push(tempData[i].MOQ);
                } // if
                else {
                    hasError = true;
                } // else
            } // for

            if (number.length <= 0) {
                isInvalid_DB.value = true;
                validation_err_msg.value = app.appContext.config.globalProperties.$t("monthlyPRpageLang.no_req_amount_to_submit");

                notyf.open({
                    type: "warning",
                    message: app.appContext.config.globalProperties.$t("basicInfoLang.nodata"),
                    duration: 3000, //miliseconds, use 0 for infinite duration
                    ripple: true,
                    dismissible: true,
                    position: {
                        x: "right",
                        y: "bottom",
                    },
                });

                $("body").loadingModal("hide");
                $("body").loadingModal("destroy");
                return;
            } // if
            else if (hasError) {
                isInvalid_DB.value = true;
                validation_err_msg.value = app.appContext.config.globalProperties.$t("monthlyPRpageLang.some_data_has_no_amount");
                notyf.open({
                    type: "warning",
                    message: app.appContext.config.globalProperties.$t("monthlyPRpageLang.some_data_has_no_amount"),
                    duration: 5000, //miliseconds, use 0 for infinite duration
                    ripple: true,
                    dismissible: true,
                    position: {
                        x: "right",
                        y: "bottom",
                    },
                });
            } // else if

            // console.log(total_price_other_currency); // test
            // return;
            let start = Date.now();
            let result = await sendBuylistMail(
                number, pName, spec, unit_price,
                nowNeed, nextNeed, stock, in_transit, req_amount,
                total_price_default_currency, total_price_default_currency_name, total_price_other_currency, moq
            );

            let timeTaken = Date.now() - start;
            console.log("Total time taken : " + timeTaken + " milliseconds");
            $("body").loadingModal("hide");
            $("body").loadingModal("destroy");

            if (selectedOnly || needConfirm2.value) {
                let checkedPN = checkedRows.map(obj => obj.);
                // Remove items from MPS_PN_Array if not in checkedPN
                for (let i = MPS_PN_Array.length - 1; i >= 0; i--) {
                    if (!checkedPN.includes(MPS_PN_Array[i])) {
                        MPS_PN_Array.splice(i, 1);
                        MPS_90PN_Array.splice(i, 1);
                    } // if
                } // for

                // Remove items from nonMPS_PN_Array if not in checkedPN
                for (let i = nonMPS_PN_Array.length - 1; i >= 0; i--) {
                    if (!checkedPN.includes(nonMPS_PN_Array[i])) {
                        nonMPS_PN_Array.splice(i, 1);
                    } // if
                } // for
            } // if
            // ------- quick test --------
            // result = await submitBuylist(
            //     number, pName, spec, unit_price,
            //     nowNeed, nextNeed, stock, in_transit, req_amount,
            //     total_price_default_currency, total_price_default_currency_name,
            //     total_price_other_currency, moq, nonMPS_PN_Array, MPS_90PN_Array, MPS_PN_Array
            // );
            // result = "failed";
            // ------- ---------- --------

            if (result === "success") {
                result = await submitBuylist(
                    number, pName, spec, unit_price,
                    nowNeed, nextNeed, stock, in_transit, req_amount,
                    total_price_default_currency, total_price_default_currency_name,
                    total_price_other_currency, moq, nonMPS_PN_Array, MPS_90PN_Array, MPS_PN_Array
                );

                if (result === "success") {
                    uploadToDBReady.value = false;
                    notyf.open({
                        type: "success",
                        message: app.appContext.config.globalProperties.$t("monthlyPRpageLang.total") + " " + data.length + " " + app.appContext.config.globalProperties.$t("monthlyPRpageLang.record") + " " + app.appContext.config.globalProperties.$t("monthlyPRpageLang.change") + " " + app.appContext.config.globalProperties.$t("monthlyPRpageLang.success"),
                        duration: 3000, //miliseconds, use 0 for infinite duration
                        ripple: true,
                        dismissible: true,
                        position: {
                            x: "right",
                            y: "bottom",
                        },
                    });
                } // if
                else {
                    notyf.open({
                        type: "error",
                        message: app.appContext.config.globalProperties.$t("checkInvLang.update_failed"),
                        duration: 3000, //miliseconds, use 0 for infinite duration
                        ripple: true,
                        dismissible: true,
                        position: {
                            x: "right",
                            y: "bottom",
                        },
                    });
                } // else
            } // if
            else {
                notyf.open({
                    type: "error",
                    message: app.appContext.config.globalProperties.$t("checkInvLang.sent_email_failed"),
                    duration: 3000, //miliseconds, use 0 for infinite duration
                    ripple: true,
                    dismissible: true,
                    position: {
                        x: "right",
                        y: "bottom",
                    },
                });
            } // else

            await getCurrency();
        } // onSendClick

        watch(Currency, async () => {
            await triggerModal();
            data.splice(0);
            if (Currency.value === "Not Found") {
                notyf.open({
                    type: "error",
                    message: app.appContext.config.globalProperties.$t("monthlyPRpageLang.exchangerate_notfound"),
                    duration: 3000, //miliseconds, use 0 for infinite duration
                    ripple: true,
                    dismissible: true,
                    position: {
                        x: "right",
                        y: "bottom",
                    },
                });
                $("body").loadingModal("hide");
                $("body").loadingModal("destroy");
            } // if

            let today = new Date();
            let twentyDaysAgo = new Date();
            twentyDaysAgo.setDate(today.getDate() - 20);
            sessionStorage.clear();
            sessionStorage.setItem("sxbisn", null);
            sessionStorage.setItem("sxbsend", null);
            sessionStorage.setItem("sxbbegin", JSON.stringify(twentyDaysAgo.toISOString().split('T')[0]));
            sessionStorage.setItem("sxbend", JSON.stringify(today.toISOString().split('T')[0]));
            await getMats();

            nonMPS_PN_Array = [];
            MPS_PN_Array = [];
            MPS_90PN_Array = [];
            isInvalid.value = false;
            let exchange_table = Currency.value.rates;

            // if all is good, proceed on generating the CombinedPRTable
            if (isInvalid.value === false) {
                await getMats_Buylist();
                MPSData.value = JSON.parse(mats.value).data;
                await getMats_nonMonthly();
                nonMPSData.value = JSON.parse(mats.value).data;

                // console.log(MPSData.value); // test
                // console.log(nonMPSData.value); // test

                let singleEntry = {};
                let tempAll = [];
                for (let i = 0; i < MPSData.value.length; i++) {
                    singleEntry. = MPSData.value[i]..toString().trim();
                    singleEntry. = MPSData.value[i]..toString().trim();
                    singleEntry. = MPSData.value[i]..toString().trim();
                    singleEntry. = MPSData.value[i]..toString().trim();
                    singleEntry. = parseFloat(MPSData.value[i].);
                    singleEntry. = parseInt(MPSData.value[i].MPS) * parseFloat(MPSData.value[i].) / 1000;
                    singleEntry. = parseInt(MPSData.value[i].MPS) * parseFloat(MPSData.value[i].) / 1000;
                    if (MPSData.value[i].total_stock === null) {
                        singleEntry. = 0;
                    } else {
                        singleEntry. = parseInt(MPSData.value[i].total_stock);
                    } // if else

                    if (MPSData.value[i].in_transit === null) {
                        singleEntry. = 0;
                    } else {
                        singleEntry. = Math.round(parseFloat(MPSData.value[i].in_transit));
                    } // if else

                    // Don't care, will be calculated below
                    singleEntry. = Math.ceil(singleEntry. + singleEntry.);

                    singleEntry. = parseFloat((singleEntry. * singleEntry.).toFixed(5));
                    singleEntry. = MPSData.value[i]..toString().trim().toUpperCase();
                    if (singleEntry. == "RMB") {
                        singleEntry. = parseFloat((singleEntry. * (exchange_table['USD'] / exchange_table['CNY'])).toFixed(5));
                    } // if
                    else {
                        singleEntry. = parseFloat((singleEntry. * (exchange_table['USD'] / exchange_table[singleEntry.])).toFixed(5));
                    } // else
                    singleEntry.MOQ = parseInt(MPSData.value[i].MOQ.toString().trim());
                    singleEntry.id = i;
                    tempAll.push(singleEntry);
                    MPS_PN_Array.push(singleEntry.);
                    MPS_90PN_Array.push(MPSData.value[i].90);
                    singleEntry = {};
                } // for

                for (let i = 0; i < nonMPSData.value.length; i++) {
                    singleEntry. = nonMPSData.value[i]..toString().trim();
                    singleEntry. = nonMPSData.value[i]..toString().trim();
                    singleEntry. = nonMPSData.value[i]..toString().trim();
                    singleEntry. = nonMPSData.value[i]..toString().trim();
                    singleEntry. = parseFloat(nonMPSData.value[i].);
                    singleEntry. = parseFloat(nonMPSData.value[i].);
                    singleEntry. = parseFloat(nonMPSData.value[i].);
                    if (nonMPSData.value[i].total_stock === null) {
                        singleEntry. = 0;
                    } else {
                        singleEntry. = parseInt(nonMPSData.value[i].total_stock);
